import ClassroomCard from '../components/Search/ClassroomCard'
import SchoolCard from '../components/Search/SchoolCard'
import {withRouter} from "react-router-dom";
import Offline from '../components/Offline'
import {Component} from 'react'
class Search extends Component{
    constructor(props){
        super(props);
        this.state = {
            result:null,
            loaded:false,

        }
    }
    componentDidMount(){
        if(navigator.onLine){
            let city = localStorage.getItem('city');
            let searchFor;
        let {school, classroom} = this.props.match.params;
        let options;
        if(!school){
            options = {
                city
            }
            searchFor = 'schools'
        }
        else if(school && !classroom){
options = {
    city,
    school
}

searchFor = 'schools'
        }
        else if(school && classroom){
options = {
    city,
    school,
    classroom
}
searchFor = 'classrooms'
        }
        console.log(options)
        fetch('https://api.yundu.co/search',{
            method: 'POST',
            headers: {
                "Content-Type": "application/json;charset=utf-8",
              },
              body: window.JSON.stringify(options)
        }).then(response => response.json()).then(response =>{
            let mapRes;
            /*let response = {
                status:'ok',
                data:[{
                    number:'7g',
                    verified:true,
                    description:'fghjk',
                    avatar:'no'
                }]
            }*/
            if(response.status === 'ok'){
                if(searchFor === 'schools'){
                    let i = 0;
                    mapRes = response.data.map((type) =>{
                        let obj = response.data[i];
                        i++
                        let number;
                        if(obj.number.includes('k')){
                            let splitted = obj.number.split('k')
                            number = `–®–∫–æ–ª–∞ ‚Ññ${splitted[0]}, –∫–æ—Ä–ø—É—Å ${splitted[1]}`
                        }
                        else{
                            number = `–®–∫–æ–ª–∞ ‚Ññ${obj.number}`
                        }
                            return(
                    <SchoolCard key={i} verified={obj.verified} number={number} description={obj.description}/>
                            )
                    });
                }
                else{
                    let i = 0;
mapRes = response.data.map((type) =>{
    let obj = response.data[i];
    let transliterateData = {"a":"–ê","b":"–ë","v":"–í","g":"–ì","d":"–î","e":"–ï","yo":"—ë","gh":"–∂","z":"–∑","i":"–∏","ik":"–π","k":"–∫","l":"–ª","m":"–º","n":"–Ω","o":"–æ","p":"–ø","r":"—Ä","s":"—Å","t":"—Ç","y":"—É","f":"—Ñ","h":"—Ö","c":"—Ü","ch":"—á","sh":"—à","shh":"—â","yy":"—ã","ye":"—ç","u":"—é","ya":"—è"}
let number;
        let res = obj.number.slice(0, -1) + transliterateData[obj.number.slice(obj.number.length - 1)]
        number = `${res} –∫–ª–∞—Å—Å`
    i++
        return(
<ClassroomCard key={i} verified={obj.verified} number={number} description={obj.description} avatar={obj.avatar}/>
        )
});
                }
                
this.setState({
    result:mapRes,
    loaded:true
})
            }
            else{
                if(response.status == 'not found'){
                    this.setState({
                        result:<p style={{textAlign:'center',fontSize:'1.1em',marginTop:5}}>–ù–µ –Ω–∞–π–¥–µ–Ω–æ.</p>,
                        loaded:true
                    })
                }
                else{
                this.setState({
                    result:<p style={{textAlign:'center',fontSize:'1.1em',marginTop:5}}>–ù–µ–∏–∑–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞.</p>,
                    loaded:true
                })
            }
            }
        })
    }
    else{
        this.setState({
            result:<Offline/>,
            loaded:true
        })
    }
    }
    render(){
    if(!this.props.login()){
        window.location.href = 'https://yundu.co/login'
    }
    else{
        let {school, classroom} = this.props.match.params;
        let searchType;
    if(school && classroom){
        searchType = '–∫–ª–∞—Å—Å–∞–º'
    }
    else if(school && !classroom){
        searchType = '—à–∫–æ–ª–∞–º'
    }
    else if(!school && !classroom){
        window.location.href = '/'
    }
    document.title = `–ü–æ–∏—Å–∫ –ø–æ ${searchType} | Yundu`
        return(
            <>
            <h3 style={{fontSize:'1.3rem'}}>–ü–æ–∏—Å–∫ –ø–æ {searchType}</h3>
            {!this.state.loaded ? searchType === '–∫–ª–∞—Å—Å–∞–º' ? <><ClassroomCard loading/><ClassroomCard loading/></> : <><SchoolCard loading/><SchoolCard loading/></> : null}
            {this.state.result}
            </>
        )
    }
}
}
export default withRouter(Search)